.contact-us-list {
  list-style: none;
}

.contact-us-list li {
  padding: 4px;
}

.contact-us-list li i {
  color: #113e6d;
}

.branch-details-div {
  text-align: center;
  -webkit-align-items: center;
  align-items: center;
  border-radius: 5px;
  border: 1px solid #e9e9e9;
  padding: 10px;
  margin-bottom: 20px;
}

.country-img {
  display: inline-block;
  width: 3em;
  height: 3em;
  vertical-align: middle;
}

.branch-details-div a {
  text-decoration: none;
}
